import React, { useRef } from 'react';
import { Circle } from 'react-konva';
import useImage from "use-image"
interface Props {
    shapeProps: any
    onSelect: any
    onChange: any
    onEditClick: any
}

const UCircle: React.FC<Props> = ({ shapeProps, onSelect, onChange, onEditClick }) => {
    const shapeRef = useRef(null)

    const { patternImageUrl, ...restProps } = shapeProps
    const [image] = useImage(patternImageUrl || null)
    const polRadius = restProps.radius;
    const imageWidth = image ? image.width : 10;
    const imageHeight = image ? image.height : 10;

    return (
        <React.Fragment>
            <Circle
                onClick={onSelect}
                onTap={onSelect}
                onDblClick={onEditClick}
                onDblTap={onEditClick}
                draggable
                strokeScaleEnabled={false}
                onTransformEnd={(e) => {
                    onChange({
                        ...e.target.attrs,
                    })
                }}
                onDragEnd={(e) => {
                    onChange({
                        ...shapeProps,
                        x: e.target.x(),
                        y: e.target.y(),
                    });
                }}
                {...shapeProps}
            />
        </React.Fragment>
    );
};
export default UCircle
